<template>
    <div>
        <h1>
            用户列表
        </h1>
        <table border="1">
            <tr>
                <td>编号</td>
                <td>姓名</td>
                <td>年龄</td>
                <td>生日</td>
                <td>操作</td>
            </tr>
            <tr v-for="user in users">
                <td>{{ user.id }}</td>
                <td>{{ user.name }}</td>
                <td>{{ user.age }}</td>
                <td>{{ user.bir }}</td>
                <!--javascript: 阻止默认事件-->
                <td>
                    <a href="javascript:" @click="delRow(user.id)">删除</a>
                    <!--vue属性绑定-->
                    <a :href="'#/user/edit?id='+user.id">修改</a>
                </td>
            </tr>
        </table>
        <a href="#/user/add">添加</a>
        <!--router-view,组件的切换-->
        <router-view></router-view>
    </div>


</template>

<script>
export default {
    name: "User",
    data () {
        return {
            users: []
        }
    },
    methods: {
        /*查询所有的方法*/
        findAll () {
            /*在进入该组件之前就要获取到用户集合*/
            this.$http.get("http://localhost:8088/users/findAll?page=1&rows=4").then((res) => {
                console.log(res.data);
                this.users = res.data.results;
            });
        }, delRow (id) {
            console.log(id);
            this.$http.get("http://localhost:8088/users/delete?id=" + id).then(res => {
                console.log(res);
                if (res.data.success) {
                    alert(res.data.msg + ",点击确定刷新当前数据");
                    this.findAll();
                }
            })
        }
    },
    components: {},
    created () {
        this.findAll();
    }, /*监听路由，如果路由有变化就*/
    watch: {
        $route: {
            /*变化之后的路由，变化之前的路由*/
            handler: function (val, oldVal) {
                console.log(val);
                /*添加或者修改过后，路由变更到user，变更过后重新发起查询所有请求*/
                if (val.path === '/user') {
                    this.findAll();
                }
            }
        }
    }
}
</script>

<style scoped>

</style>


