<template>
    <div>
        <h2>用户添加信息</h2>
        <form action="">
            用户名: <input v-model="user.name" type="text"><br>
            年龄: <input v-model="user.age" type="text"><br>
            生日: <input v-model="user.bir" type="text"><br>
            <br>
            <input type="button" @click="saveUserInfo" value="添加">
        </form>
    </div>

</template>

<script>
export default {
    name: "UserAdd",
    data () {
        return {
            user: {},/*上面输入值，下面绑定到user对象*/
        }
    }, methods: {
        saveUserInfo () {
            /*获取到添加数据过后，发送axios请求*/
            console.log(this.user);
            this.$http.post("http://localhost:8088/users/add", this.user).then(res => {
                console.log(res);
                if (res.data.success) {
                    this.$router.push("/user");
                }
            });
        }
    }
}
</script>

<style scoped>

</style>


